import * as React from 'react'
import { type DialogProps } from '@radix-ui/react-dialog'
import { Command as CommandPrimitive } from 'cmdk'
import { Search } from 'lucide-react'
import { cn } from '../internal/lib/utils'
import { Dialog, DialogContent } from './dialog'
export const Command = React.forwardRef<React.ElementRef<typeof CommandPrimitive>, React.ComponentPropsWithoutRef<typeof CommandPrimitive>>(({ className, ...props }, ref)=>(<CommandPrimitive ref={ref} className={cn('flex h-full w-full flex-col overflow-hidden rounded-md bg-popover text-popover-foreground', className)} {...props} />))
Command.displayName = CommandPrimitive.displayName
export const CommandDialog = ({ children, ...props }: DialogProps) => (<Dialog {...props}><DialogContent className='overflow-hidden p-0 shadow-lg'><Command className='[&_[cmdk-group-heading]]:px-2 [&_[cmdk-group-heading]]:font-medium [&_[cmdk-group-heading]]:text-muted-foreground'>{children}</Command></DialogContent></Dialog>)
export const CommandInput = React.forwardRef<React.ElementRef<typeof CommandPrimitive.Input>, React.ComponentPropsWithoutRef<typeof CommandPrimitive.Input>>(({ className, ...props }, ref)=>(<div className='flex items-center border-b px-3' cmdk-input-wrapper=''><Search className='mr-2 h-4 w-4 shrink-0 opacity-50' /><CommandPrimitive.Input ref={ref} className={cn('flex h-11 w-full rounded-md bg-transparent py-3 text-sm outline-none placeholder:text-muted-foreground disabled:opacity-50', className)} {...props} /></div>))
CommandInput.displayName = CommandPrimitive.Input.displayName
export const CommandList = React.forwardRef<React.ElementRef<typeof CommandPrimitive.List>, React.ComponentPropsWithoutRef<typeof CommandPrimitive.List>>(({ className, ...props }, ref)=>(<CommandPrimitive.List ref={ref} className={cn('max-h-[300px] overflow-y-auto overflow-x-hidden', className)} {...props} />))
CommandList.displayName = CommandPrimitive.List.displayName
export const CommandEmpty = React.forwardRef<React.ElementRef<typeof CommandPrimitive.Empty>, React.ComponentPropsWithoutRef<typeof CommandPrimitive.Empty>>((props, ref)=>(<CommandPrimitive.Empty ref={ref} className='py-6 text-center text-sm' {...props} />))
CommandEmpty.displayName = CommandPrimitive.Empty.displayName
export const CommandGroup = React.forwardRef<React.ElementRef<typeof CommandPrimitive.Group>, React.ComponentPropsWithoutRef<typeof CommandPrimitive.Group>>(({ className, ...props }, ref)=>(<CommandPrimitive.Group ref={ref} className={cn('overflow-hidden p-1 text-foreground', className)} {...props} />))
CommandGroup.displayName = CommandPrimitive.Group.displayName
export const CommandSeparator = React.forwardRef<React.ElementRef<typeof CommandPrimitive.Separator>, React.ComponentPropsWithoutRef<typeof CommandPrimitive.Separator>>(({ className, ...props }, ref)=>(<CommandPrimitive.Separator ref={ref} className={cn('-mx-1 h-px bg-border', className)} {...props} />))
CommandSeparator.displayName = CommandPrimitive.Separator.displayName
export const CommandItem = React.forwardRef<React.ElementRef<typeof CommandPrimitive.Item>, React.ComponentPropsWithoutRef<typeof CommandPrimitive.Item>>(({ className, ...props }, ref)=>(<CommandPrimitive.Item ref={ref} className={cn('relative flex cursor-default gap-2 select-none items-center rounded-sm px-2 py-1.5 text-sm outline-none data-[selected=true]:bg-accent', className)} {...props} />))
CommandItem.displayName = CommandPrimitive.Item.displayName
export const CommandShortcut = ({ className, ...props }: React.HTMLAttributes<HTMLSpanElement>) => (<span className={cn('ml-auto text-xs tracking-widest text-muted-foreground', className)} {...props} />)
CommandShortcut.displayName = 'CommandShortcut'